<template>
  <div class="bg-white">
    <section class="bg-white text-white">
         <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure>

         <div class="bg-blue-180 pb-12">
            <h2 class="text-3xl pt-6 text-white 
            md:text-6xl
            lg:text-4xl">- Enviar solicitud de reserva -</h2>
            <span class="font-Clarissa text-5xl">{{ title }}</span>


            <div class="container border-dashed border-2 rounded border-white p-5">
              <!-- REQUEST INFO  -->
              <div class="flex flex-col flex-wrap shadow-round bg-darkBlue text-xl px-10 py-4 mb-6
              md:flex-row">
                <div class="flex flex-col gap-3 items-start
                md:w-3/12">
                  <span><b>Pais: </b> {{ request.country }}</span>
                  <span v-if="(request.pole)"><b>Polo: </b> {{ request.pole }}</span>
                  <span class="capitalize" v-if="(request.product != '')"><b>Producto:</b> {{ request.product }}</span>
                </div>
                <div class="flex flex-col gap-3 items-start py-3 px-8 border rounded-2xl my-2
                md:w-9/12 md:m-0">
                  <div class="flex gap-x-3">
                    <b class="capitalize" v-if="(ishotel)">{{ request.product_name }}</b> 
                    <span class="text-base self-center" v-if="(ishotel)">({{ formatDate }})</span>
                  </div>                                    
                  <template v-if="(request.is_home_request)">
                    <div class="flex flex-col w-full gap-x-3 gap-y-6
                    md:flex-row">                      
                      <div class="flex flex-col gap-y-1 items-start
                      md:w-5/12">
                        <small class="text ml-3">Fecha</small>
                        <date-range-picker                    
                            ref="picker"
                            :locale-data="{ firstDay: 1, format: 'yyyy-mm-dd' }"
                            v-model="dateRange"
                            :time-picker="false"
                            :single-date-picker="true"
                            :autoApply="true"
                            :ranges="false"
                            class="dateRangePicker searcher w-full"
                        >
                        <template #input="picker">
                            <span class="text-white w-full text-center">
                                {{ picker.startDate | dateFormat }}
                            </span>
                        </template>
                        </date-range-picker>
                      </div>
                      <number-imput v-if="(request.product == 'Hoteles')" class="md:w-4/12" :title="'Cantidad de personas'" 
                      :min="1" 
                      :value="request.num_person" 
                      :id="1" 
                      @updVal="updPerson"></number-imput>
                    </div>
                  </template>
                  <template v-if="(ishotel)">
                    <div class="flex flex-col text-left w-6/12 gap-y-2">
                      <div class="flex gap-x-4 text-base pb-2">
                        <span><b>Habitaciones:</b> {{ request.num_rooms }}</span>
                        <span><b>Noches:</b> {{ num_nights }}</span>
                        <span><b>Personas:</b> {{ request.num_person }}</span>
                      </div>
                      <div class="flex gap-x-3 text-base" v-for="(item, index) in request.rooms" :key="index">
                        <span class="w-3/12">{{ item.type }}:</span> 
                        <span>{{ item.comb }}</span>
                      </div>
                    </div>
                  </template>
                  <span class="self-end text-2xl font-bold ml-auto" v-if="(calcPrice > 0)">Precio total:<span class="text-orange"> ${{ calcPrice }}</span></span>
                </div>                
              </div>
              <form class="flex flex-col gap-x-3 gap-y-6 flex-wrap shadow-round bg-darkBlue text-xl px-10 py-8
              md:flex-row"
              @submit="formSubmit">                
                <input 
                type="text" 
                placeholder="Nombre completo" 
                v-model="name" 
                min="0" 
                class="w-full border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2
                md:w-1/2">

                <input 
                type="text" 
                placeholder="Teléfono" 
                v-model="phone" 
                min="0" 
                class="w-full border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2
                md:w-1/5">

                <input 
                type="email" 
                placeholder="Email*" 
                v-model="email" 
                required
                min="0" 
                class="w-full border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2
                md:w-1/4">

                <textarea id="" cols="100" rows="5" 
                placeholder="Descripcion de su solicitud"
                v-model="message" 
                class="w-full border rounded-4xl text-orange bg-darkBlue text-left px-8 py-6"
                ></textarea>

                <div class="w-full flex gap-3" @click="activeTerm = !activeTerm">  
                  <span class="cursor-pointer border rounded-4xl text-orange bg-darkBlue px-2 py-1"
                  :class="(activeTerm) ? 'text-orange' : 'text-darkBlue'">
                    <i class="fas fa-check"></i>
                  </span>
                  <label class="text-orange self-center" for="term">Términos y condiciones</label>
                </div>
                <div class="flex flex-col gap-3 w-full
                md:flex-row">
                  <span class="bg-red rounded-xl p-2 px-4 text-left
                  md:w-3/5" v-show="error">{{ error_message }}</span>
                  <span class="bg-green-500 rounded-xl p-2 px-4 text-left
                  md:w-3/5" v-show="success">Error. No se pudo enviar la solicitud!!!</span>
                  <button class="uppercase bg-orange text-white text-xl rounded-4xl px-12 py-1 md:ml-auto">Enviar Solicitud</button>
                </div>
              </form>
            </div>
         </div>
         
    </section>
    
  </div>
</template>
<script>
import DateRangePicker from 'vue2-daterange-picker'
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'
import moment from 'moment'

import NumberImput from '@/components/NumberImput'
import Authorization from "@/services/user/auth.service";

export default {  
  name: "Contacto",
  props: {
    title: {
      require: false,
      default: 'Solicitud'
    },    
  },  
  components: {
    NumberImput,
    DateRangePicker
  },
  filters: {
    dateFormat(value) {
        if (value) {
            return moment(String(value)).format('DD MMM YYYY')
        }
    }
  },
  data() {
    return {
      activeTerm: false,
      num_nights: 1,
      is_hotel: true,   
      success: false,   
      error_message: 'Error. No se pudo enviar la solicitud!!!',   
      error: false,   
      dateRange: {
          startDate: moment().add(3, 'days').format('DD MMM YYYY')
      },  
      request: {
        country: 'Cuba',
        pole: '',
        date: '',
        product: 'hotel',
        product_name: 'Habana Libre',
        price: '1000',
        num_person: 4,
        num_rooms: 2,
        is_home_request: false,
        rooms: [
          {
            type: 'Junior Suit',
            comb: '1 adultos, 1 niño'
          },
          {
            type: 'Suit',
            comb: '2 adultos'
          }
        ]
      },
      name: '',
      phone: '',
      email: '',
      message: ''

    }
  },  
  created() {
    // iniciando los datos del pedido
    const info = localStorage.getItem('request_info')
    this.request = (info) ? JSON.parse(info) : this.request
  },  
  watch: {
    dateRange(d) {
      this.request.date = d.startDate
    }
  },
  computed: {
    ishotel() {
      return this.is_hotel = (this.request.rooms == 'hotel')
    },
    persons() {
      person = (this.request.product == 'hotel') ? this.request.num_person : person
      return person
    },
    formatDate() {
      const dates = this.request.date.split('/')
      let from, to, text_date
      if(dates.length > 1) {
        from = moment(dates[0])        
        to = moment(dates[1])
        this.num_nights = to.diff(from, 'days')
        text_date = from.format('DD/MM/YYYY') + ' - ' + to.format('DD/MM/YYYY')
      } else {
        text_date = moment(dates[0]).format('DD/MM/YYYY')
      }
      return text_date
    },
    calcPrice() {
      let price = this.request.price
      if(!this.is_hotel) {
        price = this.request.price * this.request.num_person
      }      
      
      return (isNaN(price)) ? 0 : price
    }
  },
  methods: {
    updPerson(obj){
        this.request.num_person = obj.val
    },
    async formSubmit() {
      const body = {
        country: this.request.country,
        pole: this.request.pole,
        date: this.request.date,
        name: this.name,
        phone: this.phone,
        email: this.email,
        message: this.message,
        is_hotel: this.is_hotel,
        num_person: this.request.num_person
      }      
      if(this.is_hotel) {
        body.num_person = this.request.num_person
        body.num_nights = this.num_nights
        body.rooms = this.request.rooms
      }

      if(this.email) {
        if(await Authorization.checkLogin('', {body}, 'post')) {
          localStorage.removeItem('request_info')
          this.success = true 
          setTimeout(() => {
              this.success = false
          }, 3000); 
        } else {
          this.error = true 
          setTimeout(() => {
              this.error = false
          }, 3000);          
        }
      } else {
        this.error = true 
        this.error_message = 'Debe de llenar todos los campos oblogatorios'
          setTimeout(() => {
              this.error_message = 'Error. No se pudo enviar la solicitud!!!'
              this.error = false
          }, 3000);          
      }
    },    
  }
};
</script>
